@font-face {
  font-family: "Poppins-Regular";
  src: url("../font/Poppins-Regular.ttf");
  src: url("../font/Poppins-Regular.ttf") format("ttf"),
    url("../font/Poppins-Regular.ttf") format("truetype");
}

* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: height 1s;
}

.hero {
  background-image: url(../images/vladimir-kudinov-KBX9XHk266s-unsplash\ 2.png);
  background-size: cover;
  background-repeat: no-repeat;
}

.hero-bg {
  height: 100vh;
  background: rgba(4, 0, 154, 0.7);
}

.left {
  width: 50% !important;
  float: left;
}

.right {
  width: 50%;
  float: right;
}

.right img {
  width: 100%;
}

.text-shadow {
  text-shadow: -2px 4px 2px rgba(0, 0, 0, 0.4);
}

.header {
  filter: drop-shadow(0px 10px 10px rgba(0, 0, 0, 0.5));
}

.body {
  background: rgba(248, 243, 212, 0.6);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}

.note {
  background: linear-gradient(
    90.17deg,
    rgba(245, 57, 57, 0) -38.48%,
    #fbcf33 145.9%
  );
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  border-radius: 3px;
}

.text-orange {
  color: #f76038;
}

.circle {
  margin: 5px 5px 0 0;
  border: 1px solid #009a75;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  text-align: center;
  color: #009a75 !important;
}
.circle-on {
  background-image: url(../icons/check.png);
  background-size: cover;
}

.card-height {
  height: 70%;
}

.seri {
  width: 70%;
  margin: auto;
}
.card-seri {
  border: 0px;
  background-color: #d9d9d9;
  height: auto !important;
}
.card-seri p {
  text-transform: uppercase;
  height: 30px;
  font-weight: bold;
  font-size: 0.6em;
}

.motherboard {
  width: 80%;
  margin: auto;
}

.card-motherboard {
  height: auto !important;
  background: #ffffff;
  border: 2px solid #04009a;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}

.card-motherboard img {
  border-radius: 10px 10px 0 0;
  width: 80%;
  object-fit: cover;
  margin: 5px auto 0px auto;
  aspect-ratio: 1/1;
}
.card-motherboard p {
  font-weight: 600;
  font-size: 0.9em;
  margin: 0 auto;
  padding: 0 0 10px 0;
  user-select: none;
}

.btn-card {
  height: auto !important;
  margin-top: 10px;
  background-color: #009a75;
  border: 0;
}
.btn-more {
  border-radius: 5px;
  color: white;
  font-size: 0.8em;
  margin: auto;
  padding: 3px 0;
}
.motherboard-text {
  width: 100%;
}
.motherboard-title {
  font-size: 1em;
  font-weight: normal;
  line-height: 1em;
  margin: 5px 15px;
  height: 30px !important;
  overflow: hidden;
}

.motherboard-price {
  margin-left: 15px;
  line-height: 1em;
}

.mystyle {
  border-width: 0px;
}

.react-multi-carousel-item {
  height: 150px !important;
}

.list-card {
  width: inherit;
  height: inherit;
  display: flex;
  justify-content: center;
  user-select: none;
}

.img-card {
  height: inherit !important;
  -webkit-user-drag: none;
  border-radius: 5px 0 0 5px;
}
.card-text p {
  margin-bottom: 0 !important;
}
.card-text h6 {
  margin-bottom: 0 !important;
}

.cards {
  width: 90% !important;
  height: 96% !important;
  display: flex;
  align-items: center;
  border: 1px solid rgb(214, 207, 207);
  border-radius: 5px;
  background-color: white;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.4);
}

.komponen {
  color: #04009a;
  border-top: 0;
  border-left: 0;
  border-right: 0;
  border-style: outset;
  padding-bottom: 2em;
}
